<template>
    <page-view class="picture">
        <div class="title">
            <span>动画壁纸</span>
            <span> / </span>
            <span>WALLPAPERS</span>
        </div>
        <div class="wallpapers">
            <Carousel :carphoto="carouselImg" />
            <div class="imgbox">
                <div class="paper" v-for="(item, index) of imgs" :key="index">
                    <img
                        v-lazy="item.ext[0].value[0].url"
                        @click="ImagePreview(item.ext[0].value[0].url)"
                    />
                    <div class="img-name">
                        {{ item.ext[1].value }}
                    </div>
                </div>
            </div>
            <div class="more-btn" @click="more">查看更多</div>
        </div>
        <page-footer />
    </page-view>
</template>
<script>
import Carousel from "../components/Carousel.vue";
import { ref, reactive, toRefs } from "vue";
import { ImagePreview } from "vant";
import Api from "@/utils/api";
export default {
    components: { Carousel, ImagePreview },
    setup() {
        const info = reactive({
            carouselImg: [],
            imgs: [],
            pageNum: 1,
        });

        const ImagePreview = (url) => {
            ImagePreview([url]);
        };

        const getPhoto = async (pageNum) => {
            let results = await Api.getInfo.getInfos({
                pageSize: 9,
                pageNum: 1,
                channelId: 179,
            });
            let imgbox = await Api.getInfo.getInfos({
                pageSize: 9,
                pageNum: info.pageNum,
                channelId: 178,
            });
            console.log(imgbox);
            console.log(results);
            results.list.forEach((item) => {
                info.carouselImg.push(item.ext[0].value[0].url);
            });
            imgbox.list.forEach((item) => {
                info.imgs.push(item);
            });
            console.log(info.imgs);
        };
        getPhoto();

        const more = () => {
            info.pageNum += 1;
            getPhoto();
        };
        return { ...toRefs(info), ImagePreview, more };
    },
};
</script>

<style lang="scss" scoped>
.picture {
    padding-top: 47px;
    background: url("https://www.bh3.com/_nuxt/img/wallpaperbg.91d33ac.jpg")
        no-repeat top;
    background-size: 100%;
    .title {
        font-size: 30px;
        font-weight: bold;
        color: #96dbff;
        text-align: center;
        line-height: 1;
        padding: 20px 0;
        span {
            &:first-child {
                color: white;
            }
            &:last-child {
                display: inline-block;
                transform: skew(-11deg);
                font-size: 20px;
                margin-left: -8px;
            }
        }
    }
    .wallpapers {
        background: rgba(255, 255, 255, 0.25);
        padding: 10px 0;
        .imgbox {
            display: flex;
            flex-wrap: wrap;
            .paper {
                height: 230px;
                margin-left: 9px;
                margin-top: 20px;
                img {
                    height: 200px;
                }
                .img-name {
                    height: 30px;
                    background-color: black;
                    margin-top: -4px;
                    color: white;
                    text-align: center;
                    line-height: 30px;
                    font-size: 12px;
                }
            }
        }
        .more-btn {
            width: 240px;
            height: 40px;
            border: 1px solid white;
            line-height: 40px;
            text-align: center;
            margin: 20px auto;
            color: white;
        }
    }
}
</style>